import React from "react"
import { Link } from "gatsby"


var parse = function() {
	var parseResults = [];
	var tagsToGet = ["p", "td", "h1", "h2", "h3", "h4", "h5", "h6", "li", "label", "pre", "code", "blockquote"];

	$.each(tagsToGet, function(index, value){
		parseResults = parseResults.concat([].slice.call(document.getElementsByTagName(value)));
	});

	return parseResults;
};

var doSearch = function(needle, haystack){
	var f = new Fuse(haystack, { keys: ['innerText']});
	var results = f.search(needle);

	return results;
};

// highlightOn optional. False = remove highlights from all elements in the results
var highlight = function(results, highlightOn){
	var resultsCount = results.length;
	$.each(results, function(index, element){
		var aValue = (1 - (index / resultsCount)) * .6 + .4; // opacity of the background

		var $element = $(element);
		if(highlightOn){
			$element.css("background-color", "rgba(255, 255, 0, " + aValue + ")");
		} else {
			$element.css("background-color", "transparent");	
		}
	});
};

$(document).ready(function(){
	//set up
	var dom = parse();
	var prevResults; //saves results from last search  to clear higlights later
	var rainbowTimer;

	// search when input changes
	$('#fuzzysearchbox, #site-search-query').on('input', function(e){
		var searchTerm = e.currentTarget.value;
		if(searchTerm){
			if(prevResults) { 
				highlightForDemo(prevResults, false); //clear highlights
			}
			var results = doSearch(searchTerm, dom); // get new results
			// highlightForDemo(results, true); //highlight new results
			prevResults = results; // set prevResults to clear later
			if(!rainbowTimer){
				rainbowTimer = setInterval(function(){
					highlightForDemo(prevResults, true);	
				}, 100)
			}

		} else {
			if(prevResults) { 
				highlightForDemo(prevResults, false); //clear highlights
				prevResults = null;
			}
			clearInterval(rainbowTimer);
			rainbowTimer = null;
		}
	});
});
// highlightOn optional. False = remove highlights from all elements in the results
var highlightForDemo = function(results, highlightOn){
	var resultsCount = results.length;
	$.each(results, function(index, element){
		var rValue = Math.floor(Math.random() * 255);
		var gValue = Math.floor(Math.random() * 255);
		var bValue = Math.floor(Math.random() * 255);

		var aValue = (1 - (index / resultsCount)) * .6 + .4; // opacity of the background

		var $element = $(element);
		if(highlightOn){
			$element.css("background-color", "rgba(" + rValue + ", " + gValue + ", " + bValue + " , " + aValue + ")");
		} else {
			$element.css("background-color", "transparent");	
		}
	});
};

const Fuzzy = () => {
	useEffect(() => {
		//set up
		var dom = parse();
		var prevResults; //saves results from last search  to clear higlights later
		var rainbowTimer;

		// search when input changes
		$('#fuzzysearchbox, #site-search-query').on('input', function(e){
			var searchTerm = e.currentTarget.value;
			if(searchTerm){
				if(prevResults) { 
					highlightForDemo(prevResults, false); //clear highlights
				}
				var results = doSearch(searchTerm, dom); // get new results
				// highlightForDemo(results, true); //highlight new results
				prevResults = results; // set prevResults to clear later
				if(!rainbowTimer){
					rainbowTimer = setInterval(function(){
						highlightForDemo(prevResults, true);	
					}, 100)
				}

			} else {
				if(prevResults) { 
					highlightForDemo(prevResults, false); //clear highlights
					prevResults = null;
				}
				clearInterval(rainbowTimer);
				rainbowTimer = null;
			}
		});

			var stringarr = ['The quick brown fox jumped over the lazy dog.', 'Romeo, Romeo, where fore art thou, Romeo.', 'I like big butts and I cannot lie, you other brothers can\'t deny.', 'It was the best of times, it was the worst of times.', 'Dat tho.', 'They see me rollin\', they hatin\', patrollin\', and tryin\' to catch me ridin\' dirty.', 'Billy Jean is not my lover, she just the girl who thinks that I am the one, but the kid is not my son.'];
			var random;
			var element, text;
			for (var i = 0; i < 500; i++) {
				element = document.createElement('P');
				random = Math.floor((Math.random() * stringarr.length));
				text = document.createTextNode(stringarr[random]);
				console.log(text);
				element.appendChild(text);
				document.getElementById('source').appendChild(element);
			}
	}, [])

	return (
		<>
		<div style="z-index: 100;">
		<div style="margin-top: 20em; margin-bottom: 1em; "><h1 style="background-color: white; color: #666666; font-size: 24px; font-weight: 300; width: 400px; text-align: center;text-transform: uppercase; padding: 1em; border: 1px solid #666666; margin: 0 auto;">What is Fuzzy Search?</h1></div>

		<input id="fuzzysearchbox" style="font-size: 16px; border: 1px solid #666666; display: block; margin: 0 auto;" type="text" />
		</div>
		<div id="source" style="z-index: -100; position: absolute; top: 0; ">
		</div>
		</>
	)
}

export default Fuzzy